import React from 'react'
import Layout from "../../components/Layout"
import ContentGrid from '../../components/ContentGrid'
import { graphql } from 'gatsby'

export default function DigiCams({ data }) {
  return (
    <Layout>
      <div>
        <p>digicams</p>
        <ContentGrid title="digicams" data={data}/>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query Digicams {
    allDataJson {
      nodes {
        digicams {
          backImg
          brand
          frontImg
          name
        }
      }
    }
  }
`
